/* Skills Slider - Fixed Hover */
.skills-slider-container {
  position: relative;
  max-width: 900px;
  margin: 5rem auto 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.skills-slider {
  position: relative;
  width: 600px;
  height: 400px;
  overflow: hidden;
  border-radius: 12px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.skill-slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
  cursor: pointer;
}

.skill-slide.active {
  opacity: 1;
}

.skill-slide-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.skill-slide-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  z-index: 1;
}

.skill-slide-title {
  color: white;
  font-size: 2.2rem;
  font-weight: bold;
  text-align: center;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.skill-slide-list {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.95);
  padding: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s ease;
  z-index: 2;
}

.skill-slide.active:hover .skill-slide-list {
  opacity: 1 !important;
  visibility: visible !important;
}

.skill-slide.active:hover .skill-slide-overlay {
  opacity: 0 !important;
  visibility: hidden !important;
}

.skill-slide-item {
  color: white;
  font-size: 1.1rem;
  margin-bottom: 0.8rem;
  display: flex;
  align-items: center;
  transform: translateX(20px);
  transition: transform 0.3s ease;
}

.skill-slide.active:hover .skill-slide-item {
  transform: translateX(0);
}

.skill-slide-bullet {
  width: 0;
  height: 0;
  border-left: 8px solid #9ca3af;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  margin-right: 1rem;
  flex-shrink: 0;
}

/* Navigation Arrows */
.slider-nav {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(255, 255, 255, 0.9);
  border: none;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  z-index: 10;
}

.slider-nav:hover {
  background: white;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.2);
}

.slider-nav.prev {
  left: -30px;
}

.slider-nav.next {
  right: -30px;
}

.slider-nav svg {
  width: 24px;
  height: 24px;
  fill: #374151;
}

/* Ensure no scrolling but allow footer to show */
